import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
import uCharts from '@/js_sdk/u-charts/u-charts/u-charts.js';
var _self;
let timer;
var canvaColumn=null;

export default {
	components:{
		uniLoadMore
	},
	data() {
		return {
			buyStop: false,
			loadingType: 'more',
			sellStop: false,
			records:[],
			dataIndex: 1,
			cWidth:'',
			cHeight:'',
			pixelRatio: 1,
			serverData:'',
			chartData: {
			  "categories": [],
			  "series": [{
				"name": "",
				"data": []
			  }]
			},
			pageNum: 1,
			pageSize: 20
		}
	},
	onReachBottom:function(){
		// if (this.dataIndex ==1){
		//     this.fetchBuy()	
		// }else if (this.dataIndex == 2){
		// 	this.fetchSell()
		// }
		if (timer != null) {
		           clearTimeout(timer);
		       }
		  timer = setTimeout(function() {
		       _self.fetchBuy();
		       }, 1000);
	},
	onShow:function(){
		this.fetchPrice()
		this.fetchBuy()
	},
	onLoad() {
		_self = this;
		this.cWidth=uni.upx2px(750);
		this.cHeight=uni.upx2px(400);
		this.fetchPrice()
		// this.getServerData();
		
		
		this.fetchPrice()
		this.fetchBuy()
	},
	methods: {
		async fetchPrice(){

			const res = await this.$http.get('/st/share_price')
			this.chartData.categories = res.data.data.categories
			let tmp = res.data.data.data
			let arr=[]
			tmp.forEach((item,index)=>{
				
				if (index%2 ==0){
					arr.push({"value": item,"color": "#586da4"})
				}else {
					arr.push({"value": item,"color": "#2b3c68"})
				}

			})
			this.chartData.series[0].data = arr
			setTimeout(function() {
				_self.showColumn("canvasColumn",_self.chartData);
			}, 500);
			console.log(this.chartData)
			// console.log(res)
		},
		async fetchBuy(){
			if (this.loadingType=''){
				return
			}
			let data ={
				pageNum: this.pageNum,
				pageSize: this.pageSize

			}
			this.loadingType='loading'
			const res = await this.$http.get('/st/queue_buy_list',{data: data})
			if (res.data.data.totalCount < this.pageSize){
				this.loadingType = ''
			}else {
				this.loadingType = 'more'
				this.pageSize += 20
			}
			this.records = res.data.data.list
			console.log(res)
		},
		async fetchSell(){
			if (this.sellStop){
				return
			}
			let data ={
				pageNum: this.pageNum,
				pageSize: this.pageSize
		
			}
			const res = await this.$http.get('/st/queue_sell_list',{data: data})
			if (res.data.data.totalCount < this.pageSize){
				this.sellStop = true
			}else {
				this.pageSize += 20
			}
			this.records = res.data.data.list
			console.log(res)
		},
		toMyorder:function(){
			uni.navigateTo({
				url: './myOrder',
				
			});
		},
		selectLabel:function(e){
			this.dataIndex = e.target.dataset.index
			if (this.dataIndex == 2){
				
				this.buyStop = false
				this.pageSize = 20
				this.fetchSell()
			}
			if (this.dataIndex == 1){
				this.pageSize = 20
				this.sellStop = false
				this.fetchBuy()
			}
			
		},
		getServerData(){
			uni.request({
				url: 'https://www.ucharts.cn/data.json',
				data:{
				},
				success: function(res) {
					console.log(res)
					//下面这个根据需要保存后台数据，我是为了模拟更新柱状图，所以存下来了
					_self.serverData=res.data.data;
					let Column={categories:[],series:[]};
					//这里我后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
					Column.categories=res.data.data.Column.categories;
					Column.series=res.data.data.Column.series;
					_self.showColumn("canvasColumn",Column);
				},
				fail: () => {
					_self.tips="网络错误，小程序端请检查合法域名";
				},
			});
		},
		showColumn(canvasId,chartData){
			canvaColumn=new uCharts({
				$this:_self,
				canvasId: canvasId,
				type: 'column',
				legend:{show:false},
				fontSize:11,
				background:'#111c30',
				pixelRatio:_self.pixelRatio,
				animation: true,
				categories: chartData.categories,
				series: chartData.series,
				enableScroll: true,//开启图表拖拽功能
				xAxis: {
					// disableGrid:true,
					gridColor:'#586da4',
					gridType: 'dash',
					labelCount: 4,
					itemCount:20,//x轴单屏显示数据的数量，默认为5个
					scrollShow:true,//新增是否显示滚动条，默认false
					scrollAlign:'left',//滚动条初始位置
					scrollBackgroundColor:'#111c30',//默认为 #EFEBEF
					scrollColor:'#2b3c68',//默认为 #A6A6A6
					
				},
				yAxis: {
					gridColor:'#586da4',
					data:[
						{
							format:(val)=>{return val.toFixed(2)},
							
						}
					],
					// disableGrid:true
					

				},
				dataLabel: false,
				width: _self.cWidth*_self.pixelRatio,
				height: _self.cHeight*_self.pixelRatio,
				extra: {
					column: {
						type:'group',
						width: 10
						// width: _self.cWidth*_self.pixelRatio*0.45/chartData.categories.length
					}
				  }
			});
			
		},
		// touchColumn(e){
		// 	canvaColumn.showToolTip(e, {
				
		// 		format: function (item, category) {
		// 			if(typeof item.data === 'object'){
		// 				return category + ' ' + item.name + ':' + item.data.value 
		// 			}else{
		// 				return category + ' ' + item.name + ':' + item.data 
		// 			}
		// 		}
		// 	});
		// },
		touchLineA(e){
						canvaColumn.scrollStart(e);
						canvaColumn.showToolTip(e, {
							
							format: function (item, category) {
								if(typeof item.data === 'object'){
									return category + ' ' + item.name + ':' + item.data.value 
								}else{
									return category + ' ' + item.name + ':' + item.data 
								}
							}
						});
					},
		moveLineA(e) {
			canvaColumn.scroll(e);
		},
		touchEndLineA(e) {
			canvaColumn.scrollEnd(e);
			//下面是toolTip事件，如果滚动后不需要显示，可不填写
			// canvaColumn.showToolTip(e, {
			// 	format: function (item, category) {
			// 		return category + ' ' + item.name + ':' + item.data 
			// 	}
			// });
		},
	}
}
